import createSelector from 'core/utils/createSelector';
import CustomTime from 'core/utils/CustomTime';
import ApiView from 'core/redux/api/views';
import { EMPTY_OBJECT } from 'core/constants';

export const legacyGetFormattedCompanyTime = createSelector(
  ['websocket.companyTime'],
  (companyTime) => {
    if (companyTime) {
      const companyTs = CustomTime.newInstance(companyTime.ms, companyTime.tz).getLocaleFriendlyTime();
      const companyTz = companyTime && companyTime.tz;
      return `${companyTs} GMT${companyTz}`;
    }
    return null;
  }
);
export const legacyGetTodayTime = createSelector(
  ['websocket.taskStats'],
  (taskStats) => {
    if (taskStats) {
      const daySecMs = (taskStats.daySec || 0) * 1000;
      const todayTimeMs = daySecMs;
      return CustomTime.friendlyDuration(todayTimeMs, true, true);
    }
    return null;
  }
);

export const legacyShouldShowGlobalSpinner = createSelector(
  ['websocket.apiStatus'],
  (apiStatus) => {
    if (apiStatus) {
      return ApiView.shouldShowGlobalSpinner({
        api: {
          status: apiStatus,
        },
      });
    }
    return false;
  }
);

const getSelectedTaskTags = createSelector(
  ['websocket.selectedTask'],
  selectedTask => (selectedTask ? selectedTask.tags : EMPTY_OBJECT)
);

export const legacyGetSelectedProjectName = createSelector(
  ['websocket.selectedProjectName'],
  e => e || null
);
export const legacyGetSelectedTaskName = createSelector(
  ['websocket.selectedTask'],
  selectedTask => (selectedTask ? selectedTask.name : null)
);
export const legacyGetSelectedTaskId = createSelector(
  ['websocket.selectedTask'],
  selectedTask => (selectedTask ? selectedTask.id : null)
);
export const legacyGetSelectedTaskLink = createSelector(
  [getSelectedTaskTags],
  tags => tags.apps_link || tags.chrome_link || null
);

export const legacyGetSelectedTaskApp = createSelector(
  [getSelectedTaskTags],
  tags => tags.apps || tags.chrome_integration || 'TD'
);
export const legacyHasSelectedTask = createSelector(
  ['websocket.selectedTask'],
  Boolean
);
