import 'app/stylesheets/contents/zoho.scss';
import CustomRender from './utils/render';
import { getTagId, createTag, querySelector, observeMutation, getElementTextContent } from './utils/globals';

const INTEGRATION_SLUG = 'zoho';

const doRenderInViewModes = (elem) => {
  const waitForDOM = setInterval(() => {
    const div = createTag('span', 'td-zoho-desk', `zoho-desk-${getTagId()}`);
    querySelector('.date', elem).appendChild(div);
    const projectName = 'Tickets';
    const taskElement = elem.querySelector('.reqsubject.f16');
    const title = 'Time Doctor Timer';
    const taskName = getElementTextContent(taskElement);
    const link = taskElement.href;
    clearInterval(waitForDOM);
    CustomRender.getGlobal(() => ({
      integration: INTEGRATION_SLUG,
      link,
      taskName,
      projectName,
      className: 'td-zoho',
      title,
    })).renderTo(div);
  }, 300);
};

const doRenderInRows = (elem) => {
  const div = createTag('span', 'td-zoho-desk-small', `zoho-desk-${getTagId()}`);
  elem.querySelector('.list-action').appendChild(div);
  const projectName = 'Tickets';
  const taskElement = elem.querySelector('.reqsubject');
  const taskName = taskElement.getAttribute('title') || getElementTextContent(taskElement);
  const link = taskElement.href;
  const title = 'Time Doctor Timer';
  CustomRender.getGlobal(() => ({
    integration: INTEGRATION_SLUG,
    link,
    taskName,
    projectName,
    className: 'td-zoho',
    title,
  })).renderTo(div);
};

const doRenderInTicketDetails = (elem) => {
  const div = createTag('span', 'td-zoho-desk', `zoho-desk-${getTagId()}`);
  elem.querySelector('.ndlePar').appendChild(div);
  const projectName = 'Tickets';
  const taskElement = elem.querySelector('#caseSubject');
  const taskName = taskElement.getAttribute('title');
  const link = window.location.href;
  const title = 'Time Doctor Timer';
  CustomRender.getGlobal(() => ({
    integration: INTEGRATION_SLUG,
    link,
    taskName,
    projectName,
    className: 'td-zoho',
    title,
  })).renderTo(div);
};

const doRenderInTicketModal = (elem) => {
  const div = createTag('span', 'td-zoho-desk', `zoho-desk-${getTagId()}`);
  elem.querySelector('.task-subline').appendChild(div);
  const projectName = 'Tickets';
  const taskElement = elem.querySelector('#taskSubjDV');
  const taskName = taskElement.getAttribute('title');
  const link = window.location.href;
  const title = 'Time Doctor Timer';
  CustomRender.getGlobal(() => ({
    integration: INTEGRATION_SLUG,
    link,
    taskName,
    projectName,
    className: 'td-zoho',
    title,
  })).renderTo(div);
};

observeMutation('div.list.posrel:not(.timedoctor)', doRenderInRows, true);
observeMutation('.qlist:not(.timedoctor)', doRenderInViewModes, true);
observeMutation('.dt-sub-cont:not(.timedoctor)', doRenderInTicketDetails, true);
observeMutation('#taskHeaderContents:not(.timedoctor)', doRenderInTicketModal, true);
