import _ from 'lodash';
import createSelector from 'core/utils/createSelector';
import {
  BROAD_LOGIN_METHOD,
  BROAD_LOGOUT_METHOD,
  SWITCH_COMPANY_METHOD,
  BROAD_RECOVER_LOGIN_METHOD,
  LOCAL_SYNC_METHOD,
  UPLOAD_SCREEN_METHOD,
  GET_USER_INFO,
} from 'core/redux/api/constants';

const getApiId = (state) => {
  if (state.api && state.api.id && state.api.credentials && state.api.credentials.login && state.api.credentials.accessToken) {
    return state.api.id;
  }
  return null;
};

const isApiLoading = state => _.reduce(state.api.status, (res, status) => res || status === 'api.loading', false);

const hasCredentials = state => !_.isEmpty(state.api.credentials);

const isApiSignedIn = state => hasCredentials(state) && !_.isEmpty(state.user.info);

const isUserLoaded = state =>
  hasCredentials(state) && state.api.status[LOCAL_SYNC_METHOD] === 'api.ok' && state.api.status[GET_USER_INFO] === 'api.ok';

const getApiStatus = createSelector(['api.status']);

const isApiSigningIn = createSelector(
  [getApiStatus],
  res => res[BROAD_LOGIN_METHOD] === 'api.loading'
);

const isApiSigningOut = createSelector(
  [getApiStatus],
  res => res[BROAD_LOGOUT_METHOD] === 'api.loading'
);

const isApiUnused = state => _.size(state.api.status) === 0;

const isApiSwitchingCompanies = createSelector(
  [getApiStatus],
  res => res[SWITCH_COMPANY_METHOD] === 'api.loading'
);

const isApiSyncing = state =>
  state.api.status[LOCAL_SYNC_METHOD] === 'api.loading' || state.api.status[UPLOAD_SCREEN_METHOD] === 'api.loading';

const isApiSignInRetrying = createSelector(
  [getApiStatus],
  res => res[BROAD_LOGIN_METHOD] === 'api.retrying'
);

const isOffline = createSelector(
  ['api.offline'],
  Boolean
);

const isApiCheckingAuth = state =>
  state.api.status[BROAD_RECOVER_LOGIN_METHOD] === 'api.loading' ||
  isApiSigningIn(state) ||
  isApiSignInRetrying(state) ||
  isApiSigningOut(state) ||
  isApiSwitchingCompanies(state);

const getApiKey = (state) => {
  if (state.api && state.api.id && state.api.credentials && state.api.credentials.login && state.api.credentials.accessToken) {
    return state.api.credentials.key;
  }
  return null;
};
const defaultToZero = e => e || 0;
const getLastRelaxedSyncMs = createSelector(
  ['api.sync.lastRelaxedSyncMs'],
  defaultToZero
);
const getQuickSyncIntervalMs = createSelector(['api.sync.quickIntervalMs']);
const getRelaxedSyncIntervalMs = createSelector(['api.sync.relaxedIntervalMs']);
const getScreenshotSyncIntervalMs = createSelector(['api.sync.screenshotIntervalMs']);
const getBlurredSyncIntervalMultiplier = createSelector(['api.sync.blurredSyncIntervalMultiplier']);

const shouldShowGlobalSpinner = state =>
  isApiSigningIn(state) || isApiSignInRetrying(state) || isApiSigningOut(state) || isApiSwitchingCompanies(state);

const isSignedIn = state => !!getApiId(state) && !isApiSigningOut(state) && !isApiSigningIn(state) && !isApiSwitchingCompanies(state);

export default {
  getApiId,
  isApiLoading,
  isApiSignedIn,
  isUserLoaded,
  hasCredentials,
  isApiCheckingAuth,
  isApiSigningIn,
  isApiSigningOut,
  isApiUnused,
  isApiSwitchingCompanies,
  isApiSyncing,
  isApiSignInRetrying,
  isOffline,
  getApiKey,
  getLastRelaxedSyncMs,
  getQuickSyncIntervalMs,
  getRelaxedSyncIntervalMs,
  getScreenshotSyncIntervalMs,
  getBlurredSyncIntervalMultiplier,
  shouldShowGlobalSpinner,
  isSignedIn,
};
