/* eslint no-underscore-dangle: ["error", {"allow":
["_srcElements","_storage"] }] */
export default class DataStorage {
  constructor() {
    this._storage = new WeakMap();
  }
  clearAll() {
    this._storage = new WeakMap();
  }
  remove(element, key) {
    const ret = this._storage.get(element).delete(key);
    if (this._storage.get(element).size !== 0) {
      this._storage.delete(element);
    }
    return ret;
  }
  get(element, key) {
    const elemValue = this._storage.get(element);
    return elemValue ? elemValue.get(key) : null;
  }
  has(element, key) {
    return this._storage.get(element).has(key);
  }
  set(element, key, obj) {
    if (!this._storage.has(key)) {
      this._storage.set(element, new Map());
    }
    this._storage.get(element).set(key, obj);
  }
}
