/** global: MutationObserver */
import serializeError from 'serialize-error';

export const createTag = (name, className, tagId, textContent, cssText, style) => {
  const tag = document.createElement(name);
  if (className) {
    tag.className = className;
  }
  if (tagId) {
    tag.id = tagId;
  }
  if (textContent) {
    tag.textContent = textContent;
  }
  if (cssText) {
    tag.style.cssText = cssText;
  }
  if (style) {
    Object.assign(tag.style, style);
  }
  return tag;
};
export const createLink = (className, tagName, linkHref) => {
  tagName = tagName || 'a';
  linkHref = linkHref || '#';
  const link = createTag(tagName, className);
  link.style.pointerEvents = 'visible';
  if (tagName === 'a') {
    link.href = linkHref;
  }
  return link;
};
export const getContent = (title, subtitles, padding) =>
  `
    <div class="td-integration-popover td-integration-popover--signin">
      <img class="td-integration-popover--addressbar" src="${chrome.runtime.getURL('assets/addressbar.svg')}">
      <div class="td-integration-popover--title">
        <span>${title}</span>
        <img class="td-integration-popover--arrow" src="${chrome.runtime.getURL('assets/arrow.svg')}" style="padding-left: ${padding}">
      </div>
      <ol class="td-integration-popover--subtitles">
        ${subtitles.map(subtitle => `<li class="td-integration-popover--subtitle">${subtitle}</li>`).join('')}
      </ol>
    </img>
  `;
export const matches = (el, selector) =>
  (
    el.matches ||
    el.matchesSelector ||
    el.msMatchesSelector ||
    el.mozMatchesSelector ||
    el.webkitMatchesSelector ||
    el.oMatchesSelector
  ).call(el, selector);
export const pointerEventToXY = (e) => {
  const out = {
    x: 0,
    y: 0,
  };
  if (e.type === 'touchstart' || e.type === 'touchmove' || e.type === 'touchend' || e.type === 'touchcancel') {
    const touch = e.originalEvent.touches[0] || e.originalEvent.changedTouches[0];
    out.x = touch.pageX;
    out.y = touch.pageY;
  } else if (e.type === 'mousedown' || e.type === 'mouseup' || e.type === 'click') {
    out.x = e.pageX;
    out.y = e.pageY;
  }
  return out;
};
export const getPopFromElement = (element, data) => data.get(element, 'plugin_tdPopover');
export const hideAllPops = (srcElements, data) => {
  let pop = null;
  for (let i = 0; i < srcElements.length; i += 1) {
    pop = getPopFromElement(srcElements[i], data);
    if (pop) {
      pop.hide(true);
    }
  }
};
export const hideOtherPops = (currentPop, srcElements, data) => {
  let pop = null;
  for (let i = 0; i < srcElements.length; i += 1) {
    pop = getPopFromElement(srcElements[i], data);
    if (pop && pop.id !== currentPop.id) {
      pop.hide(true);
    }
  }
};
const renderTo = (selector, renderer) => {
  let i;
  let len;
  const elems = document.querySelectorAll(selector);
  for (i = 0, len = elems.length; i < len; i += 1) {
    elems[i].classList.add('timedoctor');
  }
  for (i = 0, len = elems.length; i < len; i += 1) {
    renderer(elems[i]);
  }
};

export const observeMutation = (selector, renderer, observe, mutationSelector) => {
  try {
    if (observe) {
      const observer = new MutationObserver((mutations) => {
        // If mutationSelector is defined, render the start timer link only when an element
        // matching the selector changes.
        // Multiple selectors can be used by comma separating them.
        const match = mutations.filter(mutation => mutation.target.matches(mutationSelector));
        if (mutationSelector && !match.length) {
          return;
        }
        renderTo(selector, renderer);
      });
      observer.observe(document, { childList: true, subtree: true });
    }
    renderTo(selector, renderer);
  } catch (e) {
    chrome.runtime.sendMessage({ type: 'REPORT_ERROR', error: serializeError(e), url: window.location.href });
  }
};

const $$ = (selector, element = document, condition) => {
  if (element === null) {
    return null;
  }
  if (!condition) {
    return element.querySelector(selector);
  }
  const nodeList = element.querySelectorAll(selector);
  for (let i = 0; i < nodeList.length; i += 1) {
    if (condition(nodeList[i])) {
      return nodeList[i];
    }
  }
  return null;
};
export const querySelector = (selector, element) => $$(selector, element);
export const visible = (selector, element) =>
  $$(selector, element, (el) => {
    if (!el.offsetWidth && !el.offsetHeight && !el.getClientRects().length) {
      return false;
    }
    while (el) {
      if (el === document.body) {
        return true;
      }
      if (el.style.visibility === 'hidden' || el.style.visibility === 'collapse') {
        return false;
      }
      el = el.parentElement;
    }
    return false;
  });

export const getElementInnerText = elem => (elem ? String(elem.innerText || '') : '').trim();
export const getElementTextContent = elem => (elem ? String(elem.textContent || '') : '').trim();
export const getElementValue = elem => (elem ? String(elem.value || '') : '').trim();
export const getSelectorValue = (selector, element) => getElementValue(querySelector(selector, element));
export const getSelectorTextContent = (selector, element) => getElementTextContent(querySelector(selector, element));
export const getSelectorInnerText = (selector, element) => getElementInnerText(querySelector(selector, element));
export const getTagId = () => Math.floor(Math.random() * (99999 - 10000 + 1) + 1000);
export const createButtonContainerElement = ({ type, className, id, cssText, style, textContent }) =>
  createTag(type, className, id, textContent, cssText, style);
export const getElementText = (selector) => {
  const el = typeof selector === 'string' ? querySelector(selector) : selector;
  if (!el) {
    return '';
  }

  let text = '';
  // Text node (3) or CDATA node (4) - return its text
  if (el.nodeType === 3 || el.nodeType === 4) {
    text = el.nodeValue;
    // If node is an element (1) and an img, input[type=image], or area element, return its alt text
  } else if (
    el.nodeType === 1 &&
    (el.tagName.toLowerCase() === 'img' ||
      el.tagName.toLowerCase() === 'area' ||
      (el.tagName.toLowerCase() === 'input' && el.getAttribute('type') && el.getAttribute('type').toLowerCase() === 'image'))
  ) {
    text = el.getAttribute('alt') || '';
    // Traverse children unless this is a script or style element
  } else if (el.nodeType === 1 && !el.tagName.match(/^(script|style)$/i)) {
    const children = el.childNodes;
    for (let i = 0, l = children.length; i < l; i += 1) {
      text += getElementText(children[i]);
    }
  }
  return text;
};

// This function can be used to intercept and get responses of http requests made by the host site frontend.
export const interceptRequest = (urlMatch, responseMatch, eventToListen, callback) => {
  if (document.body && document.head) {
    const xhrOverrideScript = document.createElement('script');
    xhrOverrideScript.type = 'text/javascript';
    xhrOverrideScript.innerHTML = `
    (function() {
      var XHR = XMLHttpRequest.prototype;
      var send = XHR.send;
      var open = XHR.open;
      XHR.open = function(method, url) {
          this.url = url; // the request url
          return open.apply(this, arguments);
      }
      XHR.send = function() {
          this.${eventToListen} = function() {
            if (this.url.includes('${urlMatch}')) {
                try {
                  var data = JSON.parse(this.response);
                  if (data${responseMatch ? ` && data["${responseMatch}"]` : ''}) {
                    var event = new CustomEvent("TDCE_FromPage", {detail: data});
                    window.dispatchEvent(event);
                  }
                } catch (e) {}
            }
          };
          return send.apply(this, arguments);
      };
    })();
    `;
    document.head.prepend(xhrOverrideScript);
  }
  window.addEventListener('TDCE_FromPage', callback);
};
