import _ from 'lodash';
import moment from 'moment-timezone';

const TAGS = ['CustomTime'];

const FORMAT1 = 'YYYY-MM-DDTHH:mm:ss';
const FORMAT2 = 'YYYY-MM-DD HH:mm:ss';
const FORMAT3 = 'MMM DD, YYYY hh:mm a';

/**
 * Keeps time data in both unix ms, and a formated timestamp with
 * a specific time zone offset.
 * Also includes utility methods for dates, time and durations.
 *
 * API requires timestamps with a specific timezone offset, but
 * we should use unix ms to track time within the app
 *
 * @class CustomTime
 */
export default class CustomTime {
  /**
   * Returns a boolean whether or not given date is weekend
   * @static
   * @param {date} date the date string
   */
  static isWeekend(date) {
    const day = moment(date).day();
    return day === 6 || day === 0;
  }
  /**
   * Given a number of hours with fraction like 120.5, returns a string in `120h 30min` format
   * @static
   * @param {number} hours the hours
   */
  static formatHoursFractionWithMinutes(hours) {
    return `${Math.floor(hours)}h ${Math.floor((hours - Math.floor(hours)) * 60)}min`;
  }

  /**
   * Returns a string representation of the supplied number with a minimum of len characters
   * The necessary amount of trailing prefix characters are added until the desired length is achieved
   * @static
   * @param {number} num the number
   * @param {number} len the minimum length in string representation (default is 2)
   * @param {string} prefix the trailing characters to add (default is '0')
   */
  static getNumberWithFixedLength(num, len = 2, prefix = '0') {
    let str = `${num}`;
    while (str.length < len) {
      str = `${prefix}${str}`;
    }
    return str;
  }

  /**
   * Returns a friendly string representation of the supplied duration in millis
   * @static
   * @param {number} ms
   * @param {boolean} simplify false for 'HH:mm:ss' format; true for 'H\h m\m s\s' format
   * @param {boolean} noSeconds true to omit the seconds
   */
  static friendlyDuration(ms, simplify, noSeconds) {
    const duration = moment.duration(ms || 0);
    const timeArray = [];

    if (!simplify) {
      timeArray.push(CustomTime.getNumberWithFixedLength(Math.trunc(duration.asHours())));
      timeArray.push(CustomTime.getNumberWithFixedLength(duration.minutes()));
      if (!noSeconds) {
        timeArray.push(CustomTime.getNumberWithFixedLength(duration.seconds()));
      }
      return timeArray.join(':');
    }

    const hours = Math.trunc(duration.asHours());
    if (hours) {
      timeArray.push(`${hours}h`);
    }

    if (duration.minutes()) {
      timeArray.push(`${CustomTime.getNumberWithFixedLength(duration.minutes())}m`);
    } else {
      timeArray.push('0m');
    }

    if (!noSeconds) {
      timeArray.push(`${CustomTime.getNumberWithFixedLength(duration.seconds())}s`);
    }

    return timeArray.join(' ');
  }

  /**
   * Returns a friendly string representation of duration in format 0h 0m 0s, you can exclude/include empty hours/minutes
   * @static
   * @param {number} ms
   * @param {boolean} showHoursIfZero true to include the hours if 0
   * @param {boolean} showMinutesIfZero true to include the minutes if 0
   * @param {boolean} showSeconds true to include the seconds
   */
  static friendlyDurationReport(ms, showHoursIfZero, showMinutesIfZero, showSeconds) {
    const duration = moment.duration(ms || 0);
    const timeArray = [];

    const hours = Math.trunc(duration.asHours());

    if (!hours && showHoursIfZero) {
      timeArray.push('0h');
    } else {
      timeArray.push(`${hours}h`);
    }

    if (!duration.minutes() && showMinutesIfZero) {
      timeArray.push('0m');
    } else {
      timeArray.push(`${duration.minutes()}m`);
    }

    if (duration.seconds() && showSeconds) {
      timeArray.push(`${duration.seconds()}s`);
    }

    return timeArray.join(' ');
  }

  /**
   * Returns a string representation of the moment with format 'YYYY-MM-DDTHH:mm:ss'
   * @static
   * @param {*} m moment
   */
  static formatMoment(m) {
    return m.format(FORMAT1);
  }

  /**
   * Returns a string representation of the moment with format 'YYYY-MM-DD HH:mm:ss'
   * @static
   * @param {*} m moment
   */
  static formatMomentWithoutSeparatorT(m) {
    return m.format(FORMAT2);
  }

  /**
   * Returns a string representation of the moment with format 'MMMM DD, YYYY hh:mm a'
   * @static
   * @param {*} timestamp
   */
  static formatTimestamp(timestamp, format = FORMAT3) {
    return moment.unix(timestamp).format(format);
  }

  /**
   * Returns unix timestamp
   * @static
   */
  static getTimeStamp() {
    return moment().unix();
  }

  /**
   * Returns formated string of created moment instance
   * with timezone added on UTC
   * @static
   * @param format date format string
   * @param timezone timezone string
   */
  static getDateFormated(format = 'YYYY-MM-DDTHH:mm:ss', timezone = 'GMT') {
    return moment()
      .tz('GMT')
      .tz(timezone)
      .format(format);
  }

  /**
   * Returns a string representation of the supplied offset in secs. with format '+/-HH:mm'
   * @static
   * @param {string} offsetSecondsStr
   */
  static formatOffset(offsetSecondsStr) {
    const isNegative = offsetSecondsStr.substr(0, 1) === '-';
    const m = moment.duration(isNegative ? parseInt(offsetSecondsStr.substr(1), 10) : parseInt(offsetSecondsStr, 10), 'seconds');
    return `${isNegative ? '-' : '+'}${m
      .hours()
      .toString()
      .padStart(2, '0')}:${m
      .minutes()
      .toString()
      .padStart(2, '0')}`;
  }

  /**
   * Returns a CustomTime instance with the current date/time and the supplied timezone
   * @param {String} tz - defaults to '+00:00'
   */
  static current(tz = '+00:00') {
    return CustomTime.newInstance(new Date().getTime(), tz);
  }

  /**
   * Returns a new instance of CustomTime
   * @static
   * @param {number/string/object} unixMsOrTzTs an object containing ms (unix millis), ts (string ts in format 'YYYY-MM-DDTHH:mm:ss') and tz (string offset); or a number with the unix millis; or a string ts with format 'YYYY-MM-DDTHH:mm:ss' (also requires the tz param)
   * @param {string} tz the timezone offset, which must be supplied if unixMsOrTzTs is a string timestamp or number
   */
  static newInstance(unixMsOrTzTs, tz, logger) {
    return new CustomTime(unixMsOrTzTs, tz, logger);
  }

  /**
   * Adds '+' as a prefix to the supplied string or returns the supplied value if no string is supplied
   * @param {string} offset
   */
  static getValidOffset(offset) {
    if (_.isString(offset) && !_.startsWith(offset, '-') && !_.startsWith(offset, '+')) {
      return `+${offset}`;
    }
    return offset;
  }

  /**
   * Refreshes ts and ms based in the moment instance changes
   * @private
   */
  refreshFromMoment() {
    this.time = {
      ...this.time,
      ts: CustomTime.formatMoment(this.m),
      ms: this.m.valueOf(),
    };
  }

  /**
   * @param {number/string/object} unixMsOrTzTs an object containing ms (unix millis), ts (string ts in format 'YYYY-MM-DDTHH:mm:ss' or 'YYYY-MM-DD HH:mm:ss') and tz (string offset); or a number with the unix millis; or a string ts with format 'YYYY-MM-DDTHH:mm:ss' (also requires the tz param)
   * @param {string} tz the timezone offset, which must be supplied if unixMsOrTzTs is a string timestamp or number
   */
  constructor(unixMsOrTzTs, tz, logger = console) {
    this.logger = logger;

    const isObject = _.isObject(unixMsOrTzTs) && _.isNumber(unixMsOrTzTs.ms) && _.isString(unixMsOrTzTs.ts) && _.isString(unixMsOrTzTs.tz);

    let validTz = isObject ? unixMsOrTzTs.tz : tz;
    if (_.isString(validTz) && !_.startsWith(validTz, '-') && !_.startsWith(validTz, '+')) {
      validTz = CustomTime.getValidOffset(validTz);
    }

    if (isObject) {
      this.time = { ...unixMsOrTzTs, tz: validTz };
    } else if (_.isString(unixMsOrTzTs) && _.isString(tz)) {
      const m = moment(`${unixMsOrTzTs}${validTz}`, [`${FORMAT1}Z`, `${FORMAT2}Z`]).utcOffset(validTz);
      this.time = {
        ms: m.valueOf(),
        ts: CustomTime.formatMoment(m),
        tz: validTz,
      };
    } else if (_.isNumber(unixMsOrTzTs) && _.isString(tz)) {
      this.time = {
        ms: unixMsOrTzTs,
        ts: CustomTime.formatMoment(moment.utc(unixMsOrTzTs).utcOffset(tz)),
        tz: validTz,
      };
    } else {
      logger.error(`Illegal argument unixMsOrTzTs - ${unixMsOrTzTs} tz - ${validTz}`);
    }

    if (this.time) {
      this.m = moment.utc(this.time.ms).utcOffset(this.time.tz);
    }
  }

  /**
   * Clones the CustomTime instance
   */
  clone() {
    return new CustomTime(this.time);
  }

  /**
   * Equivalent to moment.subtract
   * @param {*} args
   */
  subtract(...args) {
    this.m = this.m.subtract(...args);
    this.refreshFromMoment();
    return this;
  }

  /**
   * Equivalent to moment.add
   * @param {*} args
   */
  add(...args) {
    this.m = this.m.add(...args);
    this.refreshFromMoment();
    return this;
  }

  /**
   * Equivalent to moment.diff
   * @param {CustomTime} customTime
   * @param {string} unit as in moment.diff
   */
  diff(customTime, unit) {
    return this.m.diff(customTime.asMoment(), unit);
  }

  /**
   * Equivalent to moment.isAfter
   * @param {CustomTime} customTime
   */
  isAfter(customTime) {
    return this.m.isAfter(customTime.asMoment());
  }

  /**
   * Returns if the supplied CustomTime is in the same year (moment.year), month and day (moment.dayOfYear)
   * @param {CustomTime} customTime
   */
  isSameYearMonthDay(customTime) {
    if (this.logger && this.tz !== customTime.tz) {
      this.logger.warn(
        {
          current: this.toString(),
          minus: customTime.toString(),
          tags: [...TAGS, 'isSameYearMonthDay'],
        },
        'CustomTime.isSameYearMonthDay: trying to compare times with different tz offsets'
      );
    }

    return this.m.year() === customTime.asMoment().year() && this.m.dayOfYear() === customTime.asMoment().dayOfYear();
  }

  /**
   * Returns if the supplied CustomTime is in the same year (moment.year) and week (moment.week)
   * @param {CustomTime} customTime
   */
  isSameYearWeek(customTime) {
    if (this.logger && this.tz !== customTime.tz) {
      this.logger.warn(
        {
          current: this.toString(),
          minus: customTime.toString(),
          tags: [...TAGS, 'isSameYearWeek'],
        },
        'CustomTime.isSameYearWeek: trying to compare times with different tz offsets'
      );
    }

    return this.m.year() === customTime.asMoment().year() && this.m.week() === customTime.asMoment().week();
  }

  /**
   * Returns if the supplied CustomTime is in the same year (moment.year) and month (moment.month)
   * @param {CustomTime} customTime
   */
  isSameYearMonth(customTime) {
    if (this.logger && this.tz !== customTime.tz) {
      this.logger.warn(
        {
          current: this.toString(),
          minus: customTime.toString(),
          tags: [...TAGS, 'isSameYearMonth'],
        },
        'CustomTime.isSameYearMonth: trying to compare times with different tz offsets'
      );
    }

    return this.m.year() === customTime.asMoment().year() && this.m.month() === customTime.asMoment().month();
  }

  /**
   * Sets the current time hours, minutes, seconds and millis to 0
   */
  resetTime() {
    this.m = this.m
      .set('hour', 0)
      .set('minute', 0)
      .set('second', 0)
      .set('millisecond', 0);
    this.refreshFromMoment();
    return this;
  }

  /**
   * Return a new CustomTime instance with the same unix ms as the current instance but with a new timezone offset
   * @param {string} tz new new timezone offset
   */
  toOffset(tz) {
    if (!this.time) {
      return this;
    }

    const validTz = CustomTime.getValidOffset(tz);
    this.time.ts = CustomTime.formatMoment(moment.utc(this.time.ms).utcOffset(validTz));
    this.time.tz = validTz;
    this.m = moment.utc(this.time.ms).utcOffset(this.time.tz);
    return this;
  }

  /**
   * Gets the 'YYYY-MM-DD' string representation of the current time
   */
  getDateTs() {
    return this.time.ts.substr(0, 10);
  }

  /**
   * Gets the 'HH:mm' or 'hh:mm aa' (based on the env. locale) string representation of the current time
   */
  getLocaleFriendlyTime() {
    return this.m.format('LT');
  }

  /**
   * Returns an object with ms, ts and tz
   */
  asObject() {
    return this.time;
  }

  /**
   * Returns the current time moment
   */
  asMoment() {
    return this.m;
  }

  get ms() {
    return this.time.ms;
  }

  get tz() {
    return this.time.tz;
  }

  get ts() {
    return this.time.ts;
  }

  get tsWithoutSeparatorT() {
    return CustomTime.formatMomentWithoutSeparatorT(this.m);
  }

  toString() {
    return `${this.time.ts}${this.time.tz}`;
  }
}
