/** global: chrome */
import _ from 'lodash';
import {
  hasSelectedTask,
  isMasterConnected,
  isSignedIn,
  isSameTask,
  isTrackerRunning,
  getCeIntegrationClickupListProjectName,
} from 'core/redux/websocket/views';
import { createLink, createTag, getContent } from './globals';
import Popover from './popover';

const texts = {
  signin: {
    title: 'Please Sign In to Start Timer',
    subtitles: ['Click on the Extension icon from the top right of your browser.', 'Enter your details and Sign In.'],
  },
  connection: {
    title: 'App or Internet issue',
    subtitles: ['Please check if your desktop app is open.', 'Please check the internet connection.'],
  },
};
export default class CustomRender {
  static getGlobal(params) {
    return new CustomRender(params);
  }
  constructor(passedParams) {
    this.getParams = (fn) => {
      const params = typeof passedParams === 'function' ? passedParams(this) : passedParams;
      if (params) {
        fn(params);
      }
    };
    this.props = {};
    this.link = createLink('td-button');
    this.imgSpan = null;
    this.btnSpan = null;
    this.popover = null;
    this.performEvent = this.performEvent.bind(this);
    this.removeEvent = this.removeEvent.bind(this);
    this.link.addEventListener('click', this.performEvent);
    window.addEventListener('beforeunload', this.removeEvent);
    const that = this;
    chrome.runtime.onMessage.addListener((msg) => {
      if (msg.type === 'sendState') {
        that.updateProps(msg.state);
      }
    });
  }

  isActive(state, taskToCompare) {
    if (!hasSelectedTask(state)) {
      return false;
    }

    const trackerRunning = isTrackerRunning(state);
    if (!trackerRunning) {
      return false;
    }

    return isSameTask(state, taskToCompare);
  }

  updateProps(state) {
    this.getParams((params) => {
      this.props = {
        isActive: this.isActive(state, params),
        showErrorState: !isMasterConnected(state) || !isSignedIn(state),
        ceIntegrationClickupListProjectName: getCeIntegrationClickupListProjectName(state),
      };
      this.updateButton();
    });
  }
  startStop(e) {
    e.preventDefault();
    e.stopPropagation();
    e.target.disabled = true;

    this.getParams((params) => {
      const { taskName, projectName, integration, link } = params;
      // eslint-disable-next-line no-console
      console.log({ params });

      if (!this.props.isActive) {
        chrome.runtime.sendMessage({ type: 'ACTION_START_TASK_PROJECT', payload: { taskName, projectName, integration, link } });
      } else {
        chrome.runtime.sendMessage({ type: 'PAUSE_TRACKER', payload: { taskName, projectName, integration, link } });
      }
    });
    e.target.disabled = false;
  }
  signIn(e) {
    e.preventDefault();
    e.stopPropagation();
    const target = e.currentTarget;
    this.getParams((params) => {
      const content = getContent(texts.signin.title, texts.signin.subtitles, '18px');
      const { container, hideElement, scrollable, scrollContainer, position, elementClass } = params;
      const options = {
        placement: 'auto',
        width: 362,
        height: 233,
        content,
        container,
        hideElement,
        scrollable,
        scrollContainer,
        position,
        className: elementClass,
      };
      this.popover = Popover.getGlobal(target, options);
    });
  }
  showErrorState(e) {
    e.preventDefault();
    e.stopPropagation();
    const target = e.currentTarget;
    const content = getContent(texts.connection.title, texts.connection.subtitles, '83px');
    this.getParams((params) => {
      const { container, hideElement, scrollable, position, scrollContainer, elementClass } = params;
      const options = {
        placement: 'auto',
        width: 362,
        height: 233,
        content,
        container,
        hideElement,
        scrollable,
        scrollContainer,
        position,
        className: elementClass,
      };
      this.popover = Popover.getGlobal(target, options);
    });
  }
  performEvent(e) {
    const { showErrorState, showLoader } = this.props;
    if (showLoader) {
      return null;
    } else if (showErrorState) {
      return this.showErrorState(e);
    }
    e.target.disabled = true;
    return this.startStop(e);
  }
  removeEvent() {
    this.link.removeEventListener('click', this.performEvent);
  }
  render() {
    const classes = ['btn-icon'];
    const btnClasses = ['td-btn'];
    this.getParams((params) => {
      const { className, title } = params;
      this.link.className += ` ${className}`;
      this.link.title = _.trim(title);
      this.imgSpan = createTag('span', classes.join(' '));
      this.imgSpan.className += ' icon-start';
      this.btnSpan = createTag('span', btnClasses.join(' '), null, 'Sign in');
      this.link.appendChild(this.imgSpan);
      this.link.appendChild(this.btnSpan);
      const that = this;
      chrome.runtime.sendMessage({ type: 'initiateState' }, msg => that.updateProps(msg.state));
    });
    return this.link;
  }
  renderTo(dom, modify = e => e) {
    this.render();
    modify(this.link);
    dom.appendChild(this.link);
    return this.link;
  }
  updateButton() {
    const classes = ['btn-icon'];
    const btnClasses = ['td-btn'];
    const linkClasses = [];
    const { showLoader, showErrorState, isActive } = this.props;

    if (showLoader) {
      if (this.popover) {
        this.popover.destroy();
      }
      this.btnSpan.textContent = 'Loading...';
      return;
    }
    if (showErrorState) {
      this.btnSpan.textContent = 'Disconnected';
      return;
    }
    if (isActive) {
      classes.push('icon-stop', 'active');
      btnClasses.push('active');
      linkClasses.push('active');
    } else {
      classes.push('icon-start', 'inactive');
      linkClasses.push('inactive');
    }
    if (this.popover) {
      this.popover.destroy();
    }
    this.link.classList.remove('active', 'inactive');
    this.link.className += ` ${linkClasses.join(' ')}`;
    this.imgSpan.className = classes.join(' ');
    this.btnSpan.className = btnClasses.join(' ');
    this.btnSpan.textContent = isActive ? 'Stop Timer' : 'Start Timer';
  }
}
